<template>
  <div class="home">
    <div class="banner-box">
      <el-carousel indicator-position="outside" :height="bHeight">
        <el-carousel-item v-for="(item,index) in bannerArr" :key="index">
          <img :src="item.img" class="banner-img">
          <div class="banner-title">{{ item.title }}</div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <div class="menu-box">
      <div @click="goMenu(1)">
        <img src="../assets/menu1.jpg">
        <p>政策法规</p>
      </div>
      <div @click="goMenu(2)">
        <img src="../assets/menu2.jpg">
        <p>廉政教育</p>
      </div>
      <div @click="goMenu(3)">
        <img src="../assets/menu3.jpg">
        <p>考试测评</p>
      </div>
      <div @click="goMenu(4)">
        <img src="../assets/menu4.jpg">
