      </div>
      <div @click="goMenu(5)">
        <img src="../assets/menu5.jpg">
        <p>廉政动态</p>
      </div>
    </div>

    <div class="list-main">
      <div class="list-title">最新资讯</div>
      <div class="list-item" v-for="(item,index) in articleList" :key="index">
        <a :href="item.link">
        <div class="list-item-left">
          <p>{{item.title}}</p>
          <span>{{item.created | parseTime('{y}-{m}-{d}')}}</span>
        </div>
        <div class="liet-item-right">
          <img :src="host + item.thumb" alt="">
        </div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "Home",
  data() {
    return {
      bannerArr: [
        {
          img: require('../assets/banner.jpg'),
          title: '山东省消防救援总队跨区抗洪抢险域增援队完成任务'
        },
        {
          img: require('../assets/banner.jpg'),
          title: '山东省消防救援总队跨区抗洪抢险域增援队完成任务'
        },
        {
          img: require('../assets/banner.jpg'),
          title: '山东省消防救援总队跨区抗洪抢险域增援队完成任务'
        },
        {
          img: require('../assets/banner.jpg'),
          title: '山东省消防救援总队跨区抗洪抢险域增援队完成任务'
        }
      ],
      bHeight: 300,
      articleList: [],
      host: ''
    }
  },
  beforeMount() {
    this.bHeight = document.body.clientWidth * 420 / 750 + 'px'
  },
  created() {
    this.getArticleList()
  },
  mounted() {
    this.host = document.location.origin
  },
  methods: {
    goMenu(index) {
      if (index === 1) this.$router.push({name: 'SearchIndex'})
      if (index === 2) this.$router.push({name: 'Teach'})
      if (index === 3) alert('考试功能前后台对接中')
      if (index === 4) this.$router.push({name: 'Report'})
      if (index === 5) this.$router.push({name: 'News'})
    },
    // 获取文章列表
    getArticleList() {
      this.$axios({
        method: 'post',
        url: '/api/article/getList',
        data: {
          "pageNum": 1,
          "pageSize": 100,
          "sort": -1
        }
      }).then(res => {
        this.articleList = res.data.data.items
      })
    }
  }
}
</script>

